
import XSwitch from 'vux/src/components/x-switch/index.vue'
import Group from 'vux/src/components/group/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import XTextarea from 'vux/src/components/x-textarea/index.vue'
import Previewer from 'vux/src/components/previewer/index.vue'
import TransferDom from 'vux/src/directives/transfer-dom/index.js'

export default {
name: 'Mine',
directives: {
TransferDom
},
components: {
Previewer,
XTextarea,
XSwitch,
XInput,
Group,
XButton
},
data () {
return {
newMessage: true,
isShow1: false,
isShow2: false,
params: {
oldPassword: '',
newPassword: ''
}
}
},
methods: {
getBack () {
this.$router.go(-1)
},

postData () {
this.$vux.loading.show({
text: '加载中...'
})
let params = this.params
params['merchantId'] = this.$store.state.merchantId
this.$http.fetchPost('/merchant/update/pay/password', params).then((res) => {
this.$vux.loading.hide()
if (res.data.code === 200) {
this.$vux.toast.show({
text: '操作成功',
position: 'middle'
})
} else {
this.$vux.toast.show({
text: res.data.message,
position: 'middle',
type: 'warn'
})
}
})
}
}
}



